.skill-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 85vw;
}

.skill-ul-level {
    display: flex;
    flex-direction: row;
    width: 30vw;
    height: 16px;
}

.skill-li {
    height: auto;
    width: auto;
    margin-bottom: 0;
}
.skill-ul-level {
    height: auto;
    width: auto;
}

.level-div {
    width: 18px;
    height: 18px;
    background-color: var(--main-background);
    border: 2px black solid;
    border-radius: 50%;
    margin-left: 2px;
}
.empty {
    background-color: var(--main-gray);
}
